import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import AccordionItem from './AccordionItem.jsx';
import styles from './Accordion.module.css';

const Accordion = () => {
  const [openIndex, setOpenIndex] = useState(null);

  const handleToggle = (index) => {
    setOpenIndex(openIndex === index ? null : index);
  };

  const accordionData = [
    {
      title: " ",
      items: [
        { path: '/worktime', label: ' ' },
        { path: '/shopaddress', label: ' ' },
        { path: '/service', label: ' ' },
        { path: '/publicoffer', label: '  ' },
        { path: '/privacy', label: '' }
      ]
    },
    {
      title: " ",
      items: [
        { path: '/shipping', label: ' ' },
        { path: '/payments', label: '  ' },
        { path: '/terms', label: ' ' },
        { path: '/warranty', label: '  ' },
        { path: '/return', label: '  ' }
      ]
    },
    {
      title: "",
      items: [
        { path: '/catalog/bicycle', label: '' },
        { path: '/catalog/parts', label: '' },
        { path: '/catalog/protection', label: '' },
        { path: '/catalog/accessories', label: '' }
      ]
    }
  ];

  return (
    <div className={styles.accordion}>
      {accordionData.map((section, index) => (
        <AccordionItem
          key={index}
          title={section.title}
          isOpen={openIndex === index}
          onToggle={() => handleToggle(index)}
        >
          <ul className={styles.accordionList}>
            {section.items.map((item, itemIndex) => (
              <li key={itemIndex} className={styles.accordionItem}>
                <Link 
                  to={item.path} 
                  className={styles.accordionLink}
                  onClick={() => setOpenIndex(null)} //    
                >
                  {item.label}
                </Link>
              </li>
            ))}
          </ul>
        </AccordionItem>
      ))}
    </div>
  );
};

export default Accordion;