import { Link } from 'react-router-dom';
import styles from './bottomMenu.module.css';

import { loginIcon, cartIcon, searchIcon, catalogIcon } from '../../../assets/icons';


const BottomMenu = () => {
  const menuItems = [
    { 
      path: '/login', 
      icon: loginIcon, 
      label: '', 
      alt: '   ' 
    },
    { 
      path: '/cart', 
      icon: cartIcon, 
      label: '', 
      alt: ' ' 
    },
    { 
      path: '/search', 
      icon: searchIcon, 
      label: '', 
      alt: ' ' 
    },
    { 
      path: '/catalog', 
      icon: catalogIcon, 
      label: '', 
      alt: ' ' 
    }
  ];

  return (        
    <div className={styles.mobileOnlyContainer}>
      <nav className={styles.bottomNavigation} aria-label=" ">
        <ul className={styles.navigationList}>
          {menuItems.map((item) => (
            <li key={item.path}>
              <Link to={item.path} className={styles.navigationItem}>
                <img 
                  className={styles.navigationIcon} 
                  src={item.icon} 
                  alt={item.alt}
                  width="24"
                  height="24"
                  loading="lazy"
                />
                <span>{item.label}</span>
              </Link>
            </li>
          ))}
        </ul>
      </nav>
    </div>
  );
};

export default BottomMenu;