import React, { useState, useEffect, Fragment } from 'react';
import { Link } from 'react-router-dom';
import '../../../index.css';
import BurgerMenu from './burgerMenu/burgerMenu.jsx';
import BottomMenu from './bottomMenu/bottomMenu.jsx';
import header from './Header.module.css';

import logo from '../../../assets/images/logo/logo-header.svg';
import { phoneIcon, locationIcon, searchIcon, cartIcon, loginIcon } from '../../assets/icons';

export default function Header() {
  const [lastScrollY, setLastScrollY] = useState(0);
  const [shouldShowHeader, setShouldShowHeader] = useState(true);
  const [isScrolled, setIsScrolled] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      const currentScrollY = window.scrollY;

      if (currentScrollY <= 100) {
        //        100px
        setShouldShowHeader(true);
      } else if (currentScrollY > lastScrollY && currentScrollY > 100) {
        //     100px - 
        setShouldShowHeader(false);
      } else if (currentScrollY < lastScrollY && currentScrollY > 100) {
        //     100px - 
        setShouldShowHeader(true);
      }

      setLastScrollY(currentScrollY);
      setIsScrolled(currentScrollY > 0);
    };

    window.addEventListener('scroll', handleScroll, { passive: true });
    return () => window.removeEventListener('scroll', handleScroll);
  }, [lastScrollY]);

  return (
    <Fragment>
      <header className={`${header.header} ${!shouldShowHeader ? header.hidden : ''} ${isScrolled ? 'scrolled' : ''}`}>
        <div className={header.headerTop}>
          <Link className={header.logo} to="/">
            <img alt='  RMBike.by' src={logo} />
          </Link>
          <div className={header.headerButtons}>
            <a className={header.phoneBtn} href='tel:+375291278356'>
              <img className={header.iconPhone} alt='  ' src={phoneIcon} />
              +375 (29) 127-83-56
            </a>
            <a 
              className={header.locationBtn} 
              target='_blank' 
              rel='noopener noreferrer' 
              href='https://yandex.by/maps/-/CDhyvZ~w'
            >
              <img className={header.iconLocation} alt='  ' src={locationIcon} />
              ., .  . 5
            </a>
          </div>
          <BurgerMenu />
        </div>
        <div className={header.headerBottom}>
          <ul className={header.navMenu}>
            <li>
              <Link className={header.navbtn} to='/catalog/bycicle'></Link>
            </li>
            <li>
              <Link className={header.navbtn} to='/catalog/parts'></Link>
            </li>
            <li>
              <Link className={header.navbtn} to='/catalog/protection'></Link>
            </li>
            <li>
              <Link className={header.navbtn} to='/catalog/accessories'></Link>
            </li>
            <li>
              <Link className={header.navbtn} to='/catalog/sale'></Link>
            </li>
          </ul>
          <div className={header.iconsBtn}>
            <Link className={header.navicons} to='/search'>
              <img alt='  ' src={searchIcon} title=' ' />
            </Link>
            <Link className={header.navicons} to='/cart'>
              <img alt='  ' src={cartIcon} title=' ' />
            </Link>
            <Link className={header.navicons} to='/login'>
              <img alt='   ' src={loginIcon} title='  ' />
            </Link>
          </div>
        </div>
      </header>
      <main>
        <BottomMenu />
      </main>
    </Fragment>
  );
}